.display-component-min {
  @media only screen and (max-width: $nav-custom-size) {
    display: block;
    transition: 250ms;
  }
}

.hide-component-min {
  @media only screen and (max-width: $nav-custom-size) {
    display: none;
    transition: 250ms;
  }
}

.p-100 {
  padding: 100px;
  @media only screen and (max-width: $nav-custom-size) {
    padding: 50px;
  }
}

.p-90 {
  padding: 90px;
  @media only screen and (max-width: $nav-custom-size) {
    padding: 45px;
  }
}

.p-80 {
  padding: 80px;
  @media only screen and (max-width: $nav-custom-size) {
    padding: 40px;
  }
}

.p-70 {
  padding: 70px;
  @media only screen and (max-width: $nav-custom-size) {
    padding: 35px;
  }
}

.p-60 {
  padding: 60px;
  @media only screen and (max-width: $nav-custom-size) {
    padding: 30px;
  }
}

.p-50 {
  padding: 50px;
  @media only screen and (max-width: $nav-custom-size) {
    padding: 25px;
  }
}

.p-40 {
  padding: 40px;
  @media only screen and (max-width: $nav-custom-size) {
    padding: 20px;
  }
}

.p-30 {
  padding: 30px;
  @media only screen and (max-width: $nav-custom-size) {
    padding: 15px;
  }
}

.p-20 {
  padding: 20px;
  @media only screen and (max-width: $nav-custom-size) {
    padding: 10px;
  }
}

.p-50-x {
  padding: 0 50px;
  @media only screen and (max-width: $nav-custom-size) {
    padding: 0 25px;
  }
}

.pt-20 {
  padding-top: 20px;
}

.bordered-10 {
  border-radius: 10px;
}

.bordered-20 {
  border-radius: 20px;
}

.bordered-30 {
  border-radius: 30px;
}

.text-centered {
  text-align: center;
}

.grid-column-one {
  grid-template-columns: auto;
}

.grid-column-two {
  grid-template-columns: auto auto;
  @media only screen and (max-width: $creatives-size) {
    grid-template-columns: auto;
  }
}

.grid-column-three {
  grid-template-columns: auto auto auto;
  @media only screen and (max-width: $creatives-size) {
    grid-template-columns: auto;
  }
}

.grid-column-four {
  grid-template-columns: auto auto auto auto;
  @media only screen and (max-width: $creatives-size) {
    grid-template-columns: auto;
  }
}

h2 {
  padding: 15px 0;
}