.header {
  position: relative;
  background-color: $fantasy;
  overflow: hidden;
  width: 100%;
  height: 800px;
  &__element-circle-one {
    position: absolute;
    background-image: url("https://firebasestorage.googleapis.com/v0/b/sholomon-website-personal.appspot.com/o/circle-1.svg?alt=media&token=aecbbdca-aa65-4c63-a7b5-437a97808579");
    background-size: cover;
    width: 76px;
    height: 76px;
    z-index: 10;
  }

  &__element-circle-two {
    position: absolute;
    background-image: url("https://firebasestorage.googleapis.com/v0/b/sholomon-website-personal.appspot.com/o/circle-2.svg?alt=media&token=7fd4cedb-5c98-4c4a-b483-be72ffe5e46c");
    background-size: cover;
    width: 68px;
    height: 68px;
    z-index: 10;
  }

  &__element-box-one {
    position: absolute;
    background-image: url("https://firebasestorage.googleapis.com/v0/b/sholomon-website-personal.appspot.com/o/box-1.svg?alt=media&token=2cb905a0-c81c-4fb8-8cec-b07f76fa08b3");
    background-size: cover;
    width: 36px;
    height: 36px;
    z-index: 10;
  }

  &__element-box-two {
    position: absolute;
    background-image: url("https://firebasestorage.googleapis.com/v0/b/sholomon-website-personal.appspot.com/o/box-2.svg?alt=media&token=fa74d87a-96d7-4a2d-82da-44bcccc7a054");
    background-size: cover;
    width: 64px;
    height: 62px;
    z-index: 10;
  }

  &__element-cross {
    position: absolute;
    background-image: url("https://firebasestorage.googleapis.com/v0/b/sholomon-website-personal.appspot.com/o/cross.svg?alt=media&token=ccc34387-ec3e-4711-9a81-38daf413b4c9");
    background-size: cover;
    width: 47px;
    height: 47px;
    z-index: 10;
  }

  &__element-dots {
    position: absolute;
    background-image: url("https://firebasestorage.googleapis.com/v0/b/sholomon-website-personal.appspot.com/o/dots.svg?alt=media&token=9787030f-ff52-4e5d-adab-4efae1523af6");
    background-size: cover;
    width: 103px;
    height: 155px;
    z-index: 10;
  }

  &__art-main {
    position: absolute;
    background-image: url("https://firebasestorage.googleapis.com/v0/b/sholomon-website-personal.appspot.com/o/art.svg?alt=media&token=c3ac19a1-eb72-4bb6-858a-bcc015e283e0");
    background-repeat: no-repeat;
    background-size: contain;
    width: 800px;
    height: auto;
    padding-top: 100%;
    top: 0;
    right: 0;
    z-index: 10;

    @media only screen and (max-width: $nav-custom-size) {
      display: none;
    }
  }

  &__rotate-90 {
    @include rotate(90deg);
  }

  &__rotate-45 {
    @include rotate(45deg);
  }

  &__middle-element {
    @media only screen and (max-width: $nav-custom-size) {
      display: none;
    }
  }

  &__content {
    position: absolute;
    width: 500px;
    height: auto;
    color: $pickled-bluewood;
    top: 50%;
    left: 10%;
    z-index: 9999;
    @include transform-translate(0, -50%);

    @media only screen and (max-width: $nav-custom-size) {
      width: 100%;
      left: 0;
    }

    &--title {
      width: 100%;
      height: 4rem;
      padding: 0;
      margin: 0;
      font-family: Oswald,sans-serif;
      font-size: 2.5rem;
      text-transform: uppercase;
      text-align: center;

      @media only screen and (max-width: $nav-custom-size) {
        font-size: 2rem;
      }
    }

    &--subtitle {
      width: 100%;
      font-style: italic;
      font-size: 1.25rem;
      text-align: center;
      margin: 20px 0 50px 0;

      @media only screen and (max-width: $nav-custom-size) {
        font-size: 1rem;
      }
    }

    &--button-wrapper {
      display: flex;
      flex-wrap: wrap;
      padding: 0 70px;


      &__item {
        flex: 1;

        &--center {
          display: flex;
          justify-content: center;

          &__first-link {
              width: 150px;
              background-color: $pickled-bluewood;
              color: $white;
              padding: 10px 15px;
              float: left;
              font-weight: 700;
              text-align: center;
              text-decoration: none;
              text-transform: uppercase;
              transition: 250ms;

              &:hover {
                background-color: $te-papa-green;
                transition: 250ms;
              }
          }

          &--second-link {
            width: 150px;
            background-color: transparent;
            color: $pickled-bluewood;
            padding: 8px 15px;
            border: 2px solid $pickled-bluewood;
            float: left;
            font-weight: 700;
            text-align: center;
            text-decoration: none;
            text-transform: uppercase;
            transition: 250ms;

            &:hover {
              background-color: $pickled-bluewood;
              border: none;
              padding: 10px 15px;
              color: $white;
              transition: 250ms;
            }
          }
        }
      }
    }
  }
}