.wrapper {
  position: relative;
  width: 100%;
  height: auto;
  overflow: hidden;


  &__watermark {
    background-image: url(https://firebasestorage.googleapis.com/v0/b/sholomon-website-personal.appspot.com/o/art-2-01.svg?alt=media&token=c78fe3cd-82ed-44ac-9c81-84db1913b51f);
    -webkit-background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    width: 500px;
    height: 500px;
    opacity: 50%;
    z-index: 1;
  }

  &__darked {
    background-color: $pickled-bluewood;
    color: $white;

    &--image {
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center center;
      width: 100%;
      height: 250px;
    }
  }

  &__block {
    position: relative;
    overflow: hidden;
    margin: 20px 0;
    z-index: 10;
  }

  &__header-title {
    width: 100%;
    color: $pickled-bluewood;
    font-family: Oswald,sans-serif;
    font-size: 2.5rem;
    text-transform: uppercase;
    margin: 0;
  }

  &__grid {
    display: grid;
    width: 100%;

    &--content {
      position: relative;
      width: 100%;

      &__image {
        background-position: center center;
        background-repeat: no-repeat;
        background-size: 110%;
        background-color: #CCC;
        width: 100%;
        height: auto;
        position: relative;
        padding-top: 50%;
        box-shadow: 0 2px 15px rgba(0, 0, 0, 0.5);
        transition: 200ms;

        &--shadow {
          display: none;
          background-color: rgba($pickled-bluewood, 0.90%);
          position: absolute;
          width: 100%;
          height: 100%;
          color: $white;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;

          &__content {
            text-align: center;
            padding: 20px;
            margin: 25% 0 0 0;
            @include transform-translate(0, -50%);

            &--title {
              text-transform: uppercase;
              font-weight: 700;
              font-family: Oswald,sans-serif;
              font-size: 1.5rem;
            }

            &--description {
              padding: 10px 0 15px 0;
            }

            &--tag {
              background-color: rgba(255, 255, 255, 1);
              padding: 5px 10px;
              margin: 0 5px;
              color: $pickled-bluewood;
              border-radius: 3px;
            }
          }
        }

        &:hover &--shadow {
          display: block;
        }

        &:hover {
          background-size: 120%;
          transition: 200ms;
        }
      }
    }
  }

  &__flex {
    position: relative;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    z-index: 10;

    @media only screen and (max-width: $wrapper-size) {
      display: inline-block;
    }

    &--content {
      flex: 1 0 33%;
      width: 100%;
      height: auto;

      &__paragprah {
        display: inline-block;
        width: 100%;
        line-height: 2.5rem;
        text-indent: 75px;
        text-align: justify;

        &--button {
          display: inline-block;
          background-color: $pickled-bluewood;
          width: auto;
          height: auto;
          padding: 10px 50px;
          margin: 20px 0 0 50%;
          color: white;
          text-align: center;
          text-decoration: none;
          text-transform: uppercase;
          font-size: 1rem;
          font-family: Oswald,sans-serif;
          @include transform-translate(-50%, 0)
        }
      }

      &__paragprah-short {
        display: inline-block;
        width: 100%;
        line-height: 2rem;
        text-indent: 75px;
        text-align: justify;

      }
    }

    &--image {
      background-color: $ragoon-green;
      background-size: contain;
      background-position: center top;
      background-repeat: no-repeat;
    }
  }
}