.nav {
  display: inline-block;
  position: fixed;
  width: 100%;
  height: 90px;
  z-index: 99999;

  @media only screen and (max-width: $nav-custom-size) {
    height: auto;
  }

  &__logo {
    width: auto;
    height: auto;
    font-family: 'Montserrat', sans-serif !important;
    color: $pickled-bluewood;
    text-transform: uppercase;
    font-size: 1.15rem;
    user-select: none;
    padding: 30px;
    float: left;

    @media only screen and (max-width: $nav-custom-size) {
      width: 100%;
    }
  }

  &__links {
    position: absolute;
    width: auto;
    list-style: none;
    padding: 0;
    margin: 0;
    top: 20px;
    right: 35px;
    transition: 250ms;

    @media only screen and (max-width: $nav-custom-size) {
      width: 100%;
      position: relative;
      top: 0;
      right: 0;
      left: 0;
      float: left
    }

    & > a {
      color: inherit;
    }

    &--item {
      padding: 10px 15px;
      margin: 0 5px;
      text-transform: uppercase;
      float: left;
      cursor: pointer;

      @media only screen and (max-width: $nav-custom-size) {
        width: 100%;
        margin: 0;
        padding: 15px;
      }
    }

    &--normal {
      &:hover {
        background-color: $pickled-bluewood;
        color: $white;
      }
    }

    &--strong {
      background-color: $pickled-bluewood;
      color: $white;
      transition: 250ms;

      &:hover {
        background-color: $pickled-bluewood;
        transition: 250ms;
      }
    }

    &--active {
      border: 2px solid $pickled-bluewood;

      @media only screen and (max-width: $nav-custom-size) {
        border: none;
        background-color:$pickled-bluewood;
        color: $white;
      }
    }
  }

  &__burger {
    display: none;
    position: absolute;
    background-color: $pickled-bluewood;
    width: 50px;
    height: 45px;
    padding: 10px;
    top: 20px;
    right: 25px;
    border-radius: 5px;
    cursor: pointer;
    transition: 250ms;

    @media only screen and (max-width: $nav-custom-size) {
      display: block;
      transition: 250ms;
    }

    & > div {
      background-color: $white;
      height: 4px;

      &:first-child {
        margin: 0 0 6px 0;
      }

      &:last-child {
        margin: 6px 0 0 0;
      }
    }
  }
}